export default {
	data() {
		return {
			loading: false,
			dialog: {
				def: false,
				cs: false,
				confirm: false
			}
		};
	},

	methods: {
		openLoading() {
			this.loading = true;
			setTimeout(() => {
				this.loading = false;
			}, 1500);
		},
		open(key) {
			this.dialog[key] = true;
		},
		openConfirm() {
			this.$refs['cl-confirm'].open({
				title: '提示',
				message: '你已成功下载COOL-UNI组件， 祝你有个愉快的体验~',
				callback: ({ action }) => {
					this.$refs['cl-message'].open({
						type: 'info',
						message:
							'点击了' +
							(action === 'cancel' ? '取消' : action === 'close' ? '关闭' : '确认')
					});
				}
			});
		},
		close(key) {
			this.dialog[key] = false;
		}
	}
};
